<template>
	<div class="table-wrap">
		<div class="table_header">
			<table>
				<thead>
					<tr>
						<th :width="item.width" v-for="item in tableobj.cols">{{item.title}}</th>
					</tr>
				</thead>
			</table>
		</div>
		<div class="table_body" ref="mytable">
			<table>
				<tbody>
					<tr height="35px" v-for="(item,index) in tableobj.table">
						<td :width="subitem.width" v-for="subitem in tableobj.cols">{{item[subitem.field] | txtFn(subitem.field,index)}}</td>
					</tr>
					<tr v-if="!tableobj.table.length && loaded" class="nodata-cell">
						<td width="100%">暂无数据</td>
					</tr>
				</tbody>
			</table>
		</div>
	</div>
</template>

<script>
import { deepCopy, _initBScroll } from '../../common/fn';

export default {
	    data () {
		    return {
		    	loaded: false
		    };
  },
  props: ['tableobj'],
  created () {
    const handleScrollX = setInterval(() => {
			    if (this.tableobj.hasOwnProperty('table')) {
			       this.loaded = true;
			       clearInterval(handleScrollX);
			    }
		  	}, 500);
  },
  mounted () {
    console.log(this.tableobj);
    _initBScroll(this.$refs.mytable);
  },
  filters: {
    txtFn (val, filed, index) { // 字段为#则显示排名
      return filed == '#' ? index + 1 : val;
    }
  }
};
</script>

<style scoped lang="less">
	.table-wrap{
		height:100%;
		.table_body{
			height:calc(100% - 40px);
			overflow:hidden;
			tr:nth-child(odd){
				background:rgba(0, 131, 143, 0.3);
			}
			.nodata-cell{
				background:none!important;
				td{
					text-align: center;
					color:#00F6FF;
					padding-top:30px;
				}
			}
		}
		.table_header{
			tr{
				height:35px;
				width:100%;
			}
		}
		th,td{
			white-space:nowrap;
			font-size:14px;
			text-align: left;
			font-weight:normal;
			padding-left:10px;
		}
		th{
			color:#00F6FF;
		}
		td{
			color:#9DD1D7;
		}
	}
</style>



